.go-back {
    font-size: 20px;
    color: white;
    padding: 1%;
    background-color: #92D7F5;
    width: 5%;
    border-radius: 0 0 1vw 0;
    cursor: pointer;
}

.login {
    margin: auto;
    margin-top: 10%;
    width: 50%;
    height: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #92d7f5;
    border-radius: 5vw;
    color: white;
}

.login h1 {
    margin-top: 5%;
    font-size: 40px;
    /* margin-bottom: 3%; */
}

.login .inputs {
    display: flex;
    align-items: center;
    /* justify-content: center; */
    flex-direction: column;
    width: 100%;
    height: 100%;
}

.login .inputs input {
    margin-top: 6%;
    width: 40%;
    height: 10%;
    border: none;
    border-radius: 1vw;
    font-size: 20px;
    padding-left: 2%;
}

.login button {
    margin: auto;
    margin-top: -20%;
    width: 25%;
    height: 10%;
    border-radius: 1vw;
    border: none;
    background-color: white;
    font-size: 20px;
    cursor: pointer;
}

.modal-contaiter {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    background-color: rgba(95, 95, 95, 0.4);
    display: flex;
    align-items: center;
    justify-content: center;
    animation: start 0.5s ease
}

.modal-contaiter.hide {
    animation: end 0.5s ease
}

.modal-contaiter .modal-bg {
    width: 40%;
    height: 40%;
    background-color: white;
    border-radius: 2vw;
    display: flex;
    align-items: center;
    /* justify-content: center; */
    flex-direction: column;
}

.modal-contaiter .modal-bg .modal-text {
    font-size: 40px;
    margin-top: auto;
}

.modal-contaiter .modal-bg .ok {
    margin-top: 10%;
    margin-bottom: 5%;
    width: 30%;
    height: 20%;
    font-size: 40px;
    border: none;
    background-color: #92D7F5;
    color: white;
    border-radius: 4vw;
    cursor: pointer;
}

@keyframes start {
    0% {
        opacity: 0;
    }
    
    100% {
        opacity: 1;
    }
}

@keyframes end {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}