.start-page {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.start-page header {
    background-color: #78bcda;
    width: 100%;
    height: 8%;
    border-radius: 0 0 1vw 1vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-size: 24px;
    color: #fefefe;
}

.start-page header .main {
    margin-left: 5%;
    cursor: pointer;
}

.start-page header .in {
    margin: auto;
    margin-right: 5%;
    cursor: pointer;
}


.start-page .content {
    height: 85%;
    font-size: 30px;
    display: flex;
    align-items: center;
    color: white;
}

.start-page .content .left-side {
    width: 40%;
    margin-left: 5%;
    background-color: #92d7f5;
    padding: 1%;
    border-radius: 2vw;
}

.start-page .content .right-side {
    width: 40%;
    margin: auto;
    margin-right: 5%;
    background-color: #92d7f5;
    padding: 1%;
    border-radius: 2vw;
}

.start-page .content .bg {
    background: url(https://geraci.ru/wp-content/uploads/2021/06/autria_doctors_min-768x413.jpg) no-repeat;
    background-size: 100%;
    position: fixed;
    top: 8%;
    width: 100%;
    height: 92%;
    opacity: 0.5;
    z-index: -1;
}

.start-page button {
    width: 20%;
    height: 10%;
    margin-bottom: 3%;
    border: none;
    border-radius: 5vw;
    font-size: 25px;
    background-color: #78BCDA;
    color: white;
    cursor: pointer;
}